'use client';

import React, { useState } from 'react';
import { X, Calendar, Check } from 'lucide-react';

export default function EventModal() {
  const [isOpen, setIsOpen] = useState(true); // Set to false initially in your real app
  const [classification, setClassification] = useState('National'); // Default active state like image

  if (!isOpen) return (
    <div className="p-10 flex justify-center">
        <button onClick={() => setIsOpen(true)} className="bg-blue-600 text-white px-6 py-3 rounded-xl font-bold">
            Open Modal
        </button>
    </div>
  );

  return (
    // 1. OUTER WRAPPER: Fixed, Full Screen, Dark Overlay, Flex Centering
    <div className="fixed inset-0 z-[9999] flex items-center justify-center p-4 bg-black/60 backdrop-blur-sm animate-in fade-in duration-200">
      
      {/* 2. MODAL CARD: Fixed Width (max-w-md), Rounded corners, Dark Grey bg */}
      <div className="w-full max-w-[420px] bg-[#2A2A2A] rounded-[32px] overflow-hidden shadow-2xl ring-1 ring-white/10 flex flex-col font-sans">
        
        {/* HEADER: Blue Gradient */}
        <div className="relative px-8 py-6 bg-gradient-to-r from-[#4A90E2] to-[#5B9EEB]">
          <div className="flex justify-between items-start">
            <div>
              <h2 className="text-2xl font-black text-slate-900 tracking-tight">Define Event</h2>
              <div className="flex items-center gap-2 mt-1">
                <div className="w-1.5 h-1.5 rounded-full bg-white/60"></div>
                <p className="text-xs font-bold text-white/80 uppercase tracking-wide">Register institutional holidays</p>
              </div>
            </div>
            <button 
              onClick={() => setIsOpen(false)}
              className="w-8 h-8 flex items-center justify-center rounded-full bg-white/20 hover:bg-white/30 text-white transition-all"
            >
              <X size={18} strokeWidth={3} />
            </button>
          </div>
        </div>

        {/* BODY: Dark Grey Content */}
        <div className="p-6 space-y-5 bg-[#333333]">
          
          {/* Input: Event Label */}
          <div className="space-y-2">
            <label className="text-[10px] font-black text-gray-400 uppercase tracking-widest ml-1">Event Label</label>
            <input 
              type="text" 
              placeholder="e.g. Winter Research Break"
              className="w-full bg-[#444444] text-white placeholder:text-gray-500 text-sm font-semibold rounded-2xl px-5 py-4 outline-none focus:ring-2 focus:ring-blue-500/50 transition-all"
            />
          </div>

          {/* Input: Date */}
          <div className="space-y-2">
            <label className="text-[10px] font-black text-gray-400 uppercase tracking-widest ml-1">Event Date</label>
            <div className="relative">
              <input 
                type="date" 
                className="w-full bg-[#444444] text-gray-300 text-sm font-semibold rounded-2xl px-5 py-4 outline-none focus:ring-2 focus:ring-blue-500/50 transition-all [&::-webkit-calendar-picker-indicator]:invert [&::-webkit-calendar-picker-indicator]:opacity-50"
              />
            </div>
          </div>

          {/* Input: Description */}
          <div className="space-y-2">
            <label className="text-[10px] font-black text-gray-400 uppercase tracking-widest ml-1">Description (Optional)</label>
            <input 
              type="text" 
              placeholder="e.g. Federal public holiday"
              className="w-full bg-[#444444] text-white placeholder:text-gray-500 text-sm font-semibold rounded-2xl px-5 py-4 outline-none focus:ring-2 focus:ring-blue-500/50 transition-all"
            />
          </div>

          {/* Classification Buttons */}
          <div className="space-y-2">
            <label className="text-[10px] font-black text-white uppercase tracking-widest ml-1">Classification</label>
            <div className="grid grid-cols-2 gap-3">
              {['NATIONAL', 'CULTURAL', 'SCHOOL', 'WEEKEND'].map((type) => {
                 const isActive = classification === type.charAt(0) + type.slice(1).toLowerCase();
                 // Special styling for 'NATIONAL' to match image (Red)
                 const isNational = type === 'NATIONAL';
                 
                 return (
                  <button
                    key={type}
                    onClick={() => setClassification(type.charAt(0) + type.slice(1).toLowerCase())}
                    className={`
                      py-3 rounded-full text-[10px] font-black tracking-widest transition-all
                      ${isNational && isActive 
                        ? 'bg-red-600 text-white shadow-lg shadow-red-900/20 border border-red-500' 
                        : isActive 
                            ? 'bg-gray-500 text-white' 
                            : 'bg-[#444444] text-gray-400 hover:bg-[#555555]'
                      }
                    `}
                  >
                    {type}
                  </button>
                 );
              })}
            </div>
          </div>
        </div>

        {/* FOOTER */}
        <div className="p-6 pt-2 bg-[#333333] flex gap-3">
          <button 
            onClick={() => setIsOpen(false)}
            className="flex-1 py-3.5 rounded-full bg-[#444444] text-gray-300 font-bold text-sm hover:bg-[#505050] transition-all"
          >
            Cancel
          </button>
          <button 
            className="flex-1 py-3.5 rounded-full bg-[#A0A0A0] hover:bg-white text-[#333333] font-bold text-sm flex items-center justify-center gap-2 transition-all"
          >
            <Check size={16} strokeWidth={3} /> Finalize Registration
          </button>
        </div>

      </div>
    </div>
  );
}